.mainBody {
  border: 3px;
  border-style: solid;
  display: flex;
  flex-direction: column;
}

.headerMiddle {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.footer {
  height: 80px;
  //position: fixed;
  //left: 0;
  //bottom: 0;
  width: 100%;

  text-align: center;
}

.footer-icon {
  width: 60px;
  height: 60px;
  padding: 10px;
}

.middle-content {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  flex-direction: column;
  padding-left: 10px;
  padding-right: 10px;
}

.middle-icon {
